.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  width: 100%;
  
  margin-top: 20px;
  
  overflow-y: auto;
  overflow-x: hidden;


  scrollbar-color: cornflowerblue rgb(18, 16, 36);

  &>main {
    height: auto;
    
    width: 95%;
  }
}

.superUl {
  opacity: .92;
  display: flex;
  flex-direction: column;

  &>li{
    position: relative;
    margin: 10px 0;

    width: 100%;
    height: 200px;
  }
  
  list-style: none;
}

.buttonsSpecials{
  display: flex;
  flex-direction: column;

  width: 100%;
  
  
  
  text-align: start;
  
  background-color: rgba(38, 32, 32, 0.774);
  outline: 2px solid rgb(0, 0, 0);
  border: 2px;
  
  color: rgb(195, 221, 243);

  transition: transform .2s;
  transition-delay: .8s;
  
  &>h1{
    margin-left: 10px;
  }
  
  &>.divSpecial{
    width: 100%;
    height: 120px;
    outline: 1px solid black;
  }
}

.buttonsSpecials:hover {
  transform: scale(.92);
  transition: transform .2s;
}

.footer {
  margin-top: 1.2em;
  min-height: 40px;
  width: 100%;
  background-color: brown;
}